<template>
  <div style="min-width: 650px">
    <el-breadcrumb separator="/" class="mb-2">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品编辑</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="p-1" :body-style="{ padding: '10px' }">
      <div slot="header">
        <span>商品信息填写</span>
      </div>
      <div style="min-width: 650px;">

        <el-form :model="model" label-width="110px">
          <el-form-item label="商品分类：">
            <el-cascader
              v-model="model.categoryId"
              :options="catelist"
              :props="cateProps"
              @change="parentCateChanged"
            />
          </el-form-item>
          <el-form-item label="名称:">
            <el-input
              v-model="model.name"
              class="commAdd"
              size="small"
              placeholder="请输入商品名称"
            />
          </el-form-item>
          <el-form-item label="商品货号:">
            <el-input
              v-model="model.code"
              class="commAdd"
              size="small"
              placeholder="请输入商品货号"
            /></el-form-item>
          <el-form-item label="商品标题:" prop="title">
            <el-input
              v-model="model.title"
              placeholder="请输入商品标题"
              class="commAdd"
              size="small"
            />
          </el-form-item>
          <!-- <el-form-item label="原价:" prop="originalPrice">
            <el-input
              v-model="model.originalPrice"
              class="commAdd"
              size="small"
              placeholder="请输入原价格"
            />
          </el-form-item> -->
          <el-form-item label="规格">
            <div v-for="(item, i) in attributeItem" :key="i" class="spec-line">
              <el-dialog title="库存数量" :visible.sync="dialogValueNum" width="30%">
                <el-input v-model.number="item.inputValueNum"></el-input>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="dialogValueNum = false">取 消</el-button>
                  <el-button type="primary" @click="onSave(i)">确 定</el-button>
                </span>
              </el-dialog>
              <div v-if="item.keyName">
                <span>{{ item.keyName }}</span>
                <i class="icon el-icon-edit" @click="onUpdateSpec(item, i)"></i>
              </div>
              <el-input v-else v-model="cacheSpecification.name" placeholder="请输入规格名" size="small" style="width:200px;">
                <el-button slot="append" icon="el-icon-check" type="primary" size="mini" @click="append(item)"></el-button>
              </el-input>
              <div class="mt-1 attribute">
                <el-tag v-for="(fieldsShowItem, fieldsShowItemIndex) in item.keyValue" :key="fieldsShowItemIndex" closable @close="delSpecTag(fieldsShowItemIndex, fieldsShowItem, i)">属性：{{ fieldsShowItem.valueName }}</el-tag>
                <el-input v-model="item.inputKeyValue" placeholder="请输入属性值" size="small" style="width:200px;">
                  <el-button slot="append" icon="el-icon-check" type="primary" size="mini" @click="onAddSpecTag(i)"></el-button>
                </el-input>
              </div>
              <i class="icon el-icon-circle-close spec-deleted" @click="onDelSpec(i)"></i>
              <el-divider></el-divider>
            </div>
            <div class="mt-1">
              <el-button size="small" type="primary" :disabled="attributeItem.length >= 3" @click="onAddSpec">添加规格项目</el-button>
            </div>
          </el-form-item>
          <el-form-item>
            <el-table :data="attributeItemTable" size="mini" border stripe>
              <el-table-column label="商品规格" align="center">
                <el-table-column v-for="(item, i) in attributeItem" :key="i" :label="item.keyName" align="center">
                  <template slot-scope="scope">
                    <span>{{ scope.row.attributeArr[i] }}</span>
                  </template>
                </el-table-column>
              </el-table-column>
              <el-table-column label="原价" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.originalPrice"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="现价" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.price"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="库存" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.stock"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="imgUrl" label="配图" align="center">
                <template slot-scope="scope">
                  <el-upload class="avatar-uploader" action="http-request" :http-request="(params) => onUploadSectionFile(params, scope)" :show-file-list="false">
                    <el-image v-if="scope.row.imgUrl" :src="scope.row.imgUrl" fit="contain" class="avatar" />
                    <i v-else class="el-icon-plus avatar-uploader-icon" />
                  </el-upload>
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>
          <el-form-item label="主图上传：" prop="imageUrl">
            <el-upload
              class="avatar-uploader"
              action="http-request"
              :http-request="uploadSectionFile"
              :show-file-list="false"
            >
              <el-image
                v-if="model.imageUrl"
                :src="model.imageUrl"
                fit="contain"
                class="avatar"
              />
              <i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
          <!-- <el-form-item label="视频 :">
            <video
              :src="model.videoUrl"
              controls
              style="width:200px;height:200px"
            />
          </el-form-item> -->
          <el-form-item label="视频：">
            <el-upload
              class="avatar-uploader"
              action="http-request"
              :show-file-list="false"
              :http-request="beforeUploadVideo"
              accept=".mp4,.qlv,.qsv,.ogg,.flv,.avi,.wmv,.rmvb"
            >
              <video
                v-if="model.videoUrl"
                :src="model.videoUrl"
                class="avatar"
                controls
              /><i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
          <el-form-item label="商品详情:" prop="describes">
            <!-- <vue-editor v-model="model.describes" use-custom-image-handler style="width: 750px" @image-added="handleImageAdded"></vue-editor> -->
            <tinymce v-model="model.describes" :height="300" style="min-width: 590px;" />
          </el-form-item>
          <el-form-item label="物流包邮:" class="mt-1">
            <el-select v-model="model.logisticsType" placeholder="包邮">
              <el-option
                v-for="item in options2"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item class="text-right">
            <el-button
              type="success"
              class="btn"
              @click="commoEdit"
            >提交审核</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>

    <image-compressor-cmp ref="imageCompressorCmp" />
    
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce'
import { MIXINS_DESCRIBES_APPEND_H5 } from './mixins/event.js'
import imageCompressorCmp from '@/components/image_compressor_cmp/index.vue'

export default {
  components: {
    // eslint-disable-next-line vue/no-unused-components
    imageCompressorCmp,
    Tinymce
  },
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      addValues: [],
      cacheSpecification: [],
      jsonContent: [],
      catelist: [],
      // 商品分类
      cateProps: {
        expandTrigger: 'hover', // 次级菜单的展开方式 click / hover
        label: 'categoryName',
        value: 'id', // 指定选中值的属性
        children: 'trees',
        checkStrictly: true
      },
      options2: [
        { value: 0, label: '包邮 ' },
        { value: 1, label: '按自定义邮费规则' },
      ],
      model: {
        // 获取到的规格数据
        imageName: '', // 图片名称
        imageUrl: '', // 编辑时回显的图片
        advertisingUrl: '', // 链接地址
        item: [],
        items: [],
        categoryId: '',
        title: '',
        code: '',
        classify: '',
        checkList: [],
        checkList1: [],
        sort: '',
        state: null,
        dialogImageUrl: '',
        dialogVisible: false,
        textarea: '',
      },
      attributeItemTable: [], // 给规格表格用的数据
      attributeItem: [], // 属性
      dialogValueNum: false, // 填写库存的弹出框
      saveInventoryIndex: '', // 保存库存时选择的规格是哪一个
      saveInventoryAttributeIndex: '', // 保存库存时选择的属性是哪一个
    }
  },

  mixins: [
    MIXINS_DESCRIBES_APPEND_H5
  ],

  methods: {
    // 保存规格名
    append(item) {
      if (!this._validAttributeKey(this.cacheSpecification.name, this.model.items)) {
        return false
      }
      item.keyName = this.cacheSpecification.name
    },

    // 点击删除属性
    delSpecTag (fieldsShowItemIndex, fieldsShowItem, i) {
      this.model.items[i].keyValue.splice(fieldsShowItemIndex, 1)
      this._assignmentPrices()
      console.log('this.model.items', this.model.items)
    },
    // 提交更新
    async commoEdit() {
      if (!this.attributeItem) {
        this.$message.error('规格不能为空')
        return false
      }
      this.model.item.map((item) => {
        item.fields = JSON.parse(item.fields1)
      })
      let data = JSON.parse(JSON.stringify(this.model))
      data.describes = this._mixinsDescribesAppendH5(data.describes)
      data.items = this.attributeItem
      this.attributeItemTable.map((item) => {
        if (!item.price) {
          this.$message.error('现价不能为空')
          throw SyntaxError('现价为空强制报错停止map')
        }
        item.attrSymbolNo = item.attributeArr.join(',')
      })
      data.skus = this.attributeItemTable
      const res = await this.$http.put(this.$api.productUpdate, data)
      if (res.data.success) {
        this.$message.success('提交成功')
        this.$router.push('/commo/list')
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // // 富文本图片上传
    // async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
    //   const formData = new FormData()
    //   formData.append('file', file)
    //   const res = await this.$http.post('/common/oss/uploadFile', formData)
    //   console.log(1111, res)
    //   Editor.insertEmbed(cursorLocation, 'image', res.data.data.webUrl)
    //   resetUploader()
    // },
    // 分类js
    parentCateChanged (e) {
      this.model.categoryId = e[e.length - 1]
    },
    // 获取明细属性
    _fieldsShowItem (i) {
      let fieldsShowItem = this.model.item.map((item) => item.fieldsShow[i])
      return Array.from(new Set(fieldsShowItem))
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        )
      }
    },
    handleSelect(item) {
      console.log(item)
    },

    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },

    // 级联选择器选中项变化，会触发这个函数
    handleChange() {
      console.log(this.cateProps)
      if (this.cateProps.length === 2) {
        this.cateProps = []
      }
    },
    handleCurrentChange(row, event, column) {
      console.log(row, event, column, event.currentTarget)
    },
    handleEdit(index, row) {
      console.log(index, row)
    },

    // 编辑规格名
    onUpdateSpec (item, i) {
      item.keyName = ''
    },
    // 点击确定保存规格
    onSave (index) {
      let data = {
        valueName: this.attributeItem[this.saveInventoryIndex].inputKeyValue,
        // valueNum: this.attributeItem[index].inputValueNum
      }
      // if (this.saveInventoryAttributeIndex === '') {
      this.attributeItem[this.saveInventoryIndex].keyValue.push(data)
      // } else {
      //   this.attributeItem[this.saveInventoryIndex].keyValue[this.saveInventoryAttributeIndex].valueNum = this.attributeItem[index].inputValueNum
      // }
      this.attributeItem[this.saveInventoryIndex].inputKeyValue = ''
      this._assignmentPrices()
      this.saveInventoryAttributeIndex = ''
    },
    // 删除一个规格
    onDelSpec (index) {
      this.attributeItem.splice(index, 1)
      this._assignmentPrices()
    },

    // 私有方法----------------------------------------------------------------------
    getTreeData(data) {
      // eslint-disable-next-line no-undef
      for (var i = 0; i < data.length; i++) {
        // eslint-disable-next-line no-undef
        if (data[i].trees.length < 1) {
          // trees
          // eslint-disable-next-line no-undef
          data[i].trees = undefined
        } else {
          // trees 递归调用 本方法
          // eslint-disable-next-line no-undef
          this.getTreeData(data[i].trees)
        }
      }
      // eslint-disable-next-line no-undef
      return data
    },
    // 把组合的价格赋值到新的组合上
    _assignmentPrices () {
      this._attributeItemTable()
      this.attributeItemTable = this.attributeItemTable.map((item) => {
        console.log('this.model.skus', this.model.skus)
        this.model.skus.map((skusItem) => {
          if (item.attributeArr.join(',') === skusItem.attrSymbolNo) {
            item = skusItem
            item.attributeArr = skusItem.attrSymbolNo.split(',')
          }
        })
        return item
      })
    },

    // api--------------------------------------------------------------------------
    // 获取分类数据
    async getCateList() {
      const res = await this.$http.get(`/woho/product/category/findByTree/${localStorage.getItem('userArea')}`)
      this.catelist = this.getTreeData(res.data.data)
    },
    // 获取商品数据
    async fetch () {
      const res = await this.$http.get(
        `${this.$api.productFindById}?id=${this.id}&type=${2}`
      )
      this.model = res.data
      try {
        this.model.describes = JSON.parse(this.model.describes).goodsDetailsContent
      } catch (error) { error }
      this.model.items.map((item) => {
        this.attributeItem.push({
          keyName: item.keyName,
          inputKeyName: '',
          keyValue: item.keyValue,
          inputValueNum: '',
          inputKeyValue: '',
          status: false
        })
      })
      this._assignmentPrices()
    },
  },

  created() {
    this.getCateList()
    this.id && this.fetch()
  },
}
</script>

<style lang="scss" scoped>
@import url('../../assets/css/commponEdit.scss');
.el-icon-plus{
  border: 1px dashed #ccc;
}
.attribute{
  display: flex;
  align-items: center;
}
.el-icon-edit{
  cursor: pointer
}

.spec-line{position:relative;
  padding-left: 15px;
  .spec-deleted{position:absolute;left:0;top:0;display: none;cursor: pointer;}
  &:hover{
    .spec-deleted{display: inline;}
  }
}
</style>
